import React, { ReactNode } from 'react'
import styled, { StyledComponent } from 'styled-components'

const GridElement: StyledComponent<
  'div',
  any,
  IGridMenuProps,
  never
> = styled.div`
  height: ${(props: IGridMenuProps) => props.height || 'auto'};
  overflow: ${(props: IGridMenuProps) =>
    props.height === '100vh' ? 'hidden' : 'auto'};
  display: grid;
  grid-template-columns: repeat(
    ${(props: IGridMenuProps) => props.columns || 1},
    1fr
  );
  grid-template-rows: repeat(
    ${(props: IGridMenuProps) => props.rows || 1},
    1fr
  );
`

interface IGridMenuProps {
  children: ReactNode
  columns?: number
  rows?: number
  height?: string
}

export const Grid = ({ children, columns, rows, height }: IGridMenuProps) => (
  <GridElement columns={columns} rows={rows} height={height}>
    {children}
  </GridElement>
)
