import { Link } from 'gatsby'
import PropTypes from 'prop-types'
import React from 'react'
import styled from 'styled-components'
import Logo from './logo'
import { routes } from '../data/routes'
import { theme } from '../data/theme'

interface IHeaderProps {
  siteTitle: string
}

const StyledHeader = styled.header({
  background: 'white',
  paddingBottom: '2rem',
  paddingTop: '2rem',
})

const StyledHeaderDiv = styled.div({
  margin: '0 auto',
  maxWidth: 960,
  padding: '2rem',
})

const StyledNav = styled.nav({
  display: 'grid',
  alignItems: 'center',
  gridTemplateColumns: 'repeat(4, 1fr)',
  borderBottom: '1px solid lightgrey',
  paddingBottom: '2em',

  '& > *': {
    justifySelf: 'center',
  },
})

const StyledH1 = styled.h1({
  margin: 0,
})

const StyledLink = styled(Link)({
  color: theme.primary,
  textDecoration: 'none',

  ':hover': {
    color: theme.primary_hover,
  },
})

const StyledHeaderLogo = styled.div`
  margin: 0 auto;
  text-align: center;
`

const Header = ({ siteTitle }: IHeaderProps) => (
  <StyledHeader>
    <StyledHeaderLogo>
      <StyledLink to="/">
        <Logo size={128} strokeColor={theme.primary} strokeSize={8} fill="white" background="white" bordered={true} />
      </StyledLink>
    </StyledHeaderLogo>
    <StyledHeaderDiv>
      <StyledNav>
        {routes.map((route, i) => (
          <StyledH1 key={i}>
            <StyledLink to={route.path}>{route.title}</StyledLink>
          </StyledH1>
        ))}
      </StyledNav>
    </StyledHeaderDiv>
  </StyledHeader>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
